  opacity: 0;
  pointer-events: none;
}

.heading--hide {
  width: 1px;
  height: 1px;
  outline-offset: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  outline: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.no-scroll {
  overflow: hidden;
}

.button--small {
